define('ember-widgets/components/typeahead-component', ['exports', 'ember', 'ember-widgets/components/select-component'], function (exports, Ember, SelectComponent) {

  'use strict';

  exports['default'] = SelectComponent['default'].extend({
    layoutName: 'typeahead',
    searchFieldClass: 'form-control',
    disabled: false,

    searchView: Ember['default'].TextField.extend({
      'class': 'ember-select-input',
      valueBinding: 'parentView.query',
      focusIn: function focusIn() {
        this.set('parentView.showDropdown', true);
      }
    }),

    userDidSelect: function userDidSelect(selection) {
      this._super(selection);
      this.set('query', this.get('selection'));
    }
  });

});